.middle-section {
    position: relative;
    padding: 174px 0px;

    >div.flex.column {
        @include for-wide-layout { 
            flex-direction: row;
        }
    }
    h2 {
        flex-basis: 50%;
        padding-top: 10px;
        padding-inline-end: 51px;
        position: relative;
        max-width: 15ch;
        margin-bottom: 65px;

        @include for-normal-layout {
            max-width: 30ch;
        }

        &::before {
            content: "";
            width: 50px;
            height: 4px;
            position: absolute;
            top: -38px;
            background-color: $clr3;
        }
    }
    ul {
        flex-basis: 50%;
        gap: 44px;

        li.flex.column {
            div {
                float: revert;

                h3 {
                    color: $clr3;
                    text-align: center;
                    
                    @include for-normal-layout {
                        text-align: left;
                    }
                }
                p {
                    text-align: center;

                    @include for-normal-layout {
                        text-align: left;
                    }
                }
            }
            img {
                width: 72px;
                margin-inline-end: 38px;
                float: left;
            }
            @include for-normal-layout { 
                flex-direction: row;
            }
        }
    }
    .bg-pattern1 {
        position: absolute;
        width: 100px;
        top: 0;
        right: 0;
      

        @include for-wide-layout {
            top: unset;
            bottom: 0;
        }
    }
}