.top-section {
    position: relative;

    .bg-pattern1 {
        display: none;

        @include for-wide-layout {
            display: block;
            position: absolute;
            top: 50%;
            left: 0;
            width: 200px;
            grid-column: 1;
            transform: translate(-50%, -50%);
        }
    }
}