.action-section.flex.column {
    position: relative;
    height: 302px;
    background-color: $clr3;

    @include for-normal-layout { 
        height: 200px;
        flex-direction: row;
    }

    h2 {
        color: $clr2;
        width: 11ch;
        text-align: center;
        margin-bottom: 25px;

        @include for-normal-layout { 
            width: auto;
            margin-bottom: 0;
            margin-inline-end: 85px;
        }
    };
    .btn-contact {
        color: $clr2;
        border-color: $clr2;
    }
    .bg-pattern1 {
        position: absolute;
        bottom: 0;
        left: 0;
        width: 198px;
    }
}