.app-header {
    height: 200px;
    nav {
        width: 100%;

        .logo {
            margin-inline-end: 80px;
            width: 160px;
        }
        ul {
            display: none;

            @include for-normal-layout {
                display: flex;
                gap: 44px;
                flex-grow: 1;
            }

            li {
                &:last-child {
                    justify-content: flex-end;
                    flex-grow: 1;
                }
            }
        }
        .hamburger {
            flex-grow: 1;
            justify-content: flex-end;
            
            img {
                width: 20px;
            }

            @include for-normal-layout {
                display: none;
            }
        }
    }
    .btn-contact {
        align-self: center;
    }
}