.app-hero.flex.column {
    position: relative;
    height: 500px;

    @include for-wide-layout {
        flex-direction: row;
        align-items: center;
    }

    h1 {
        margin-top: -175px;
        max-width: 10ch;
        
        @include for-wide-layout {
            flex-basis: 635px;
            padding-inline-end: 20px;
            padding-inline-start: 89px;
            text-align: left;
            font-size: 6.25rem;
            line-height: 6.25rem;
            max-width: 20ch;
        }

        span {
            color: $clr3;
        }
    }
    p {
        text-align: center;
        max-width: 51ch;

        @include for-wide-layout {
            text-align: left;
            margin-top: -175px;
            padding-inline-start: 60px;
            flex-basis: 445px;
        }
    }
    .bg-pattern2 {
        position: absolute;
        width: 358px;
        bottom: 0;
        right: 50%;
        transform: translateX(50%);

        @include for-wide-layout {
            right: 0;
            transform: translateX(0);
        }
        
    }
}